import React, { useCallback, useEffect, useRef, useState } from 'react';
import classes from './AddPhotoFrame.module.scss';
import Button from '../../ui/Button/Button';
import Input from '../../ui/Input/Input';

const AddPhotoFrame = ({ setShowHandler, addPhoto }) => {
  
  const [http, setHttp] = useState('');
  const [name, setName] = useState('');
  const [error, setError] = useState(false);
  const [desc, setDesc] = useState('');
  const [keyWords, setKeyWords] = useState('');
  const frame = useRef();
  
  const closeFrame = e => {
    if (frame.current === e.target) {
      setShowHandler(false);
    }
  };
  
  
  useEffect(() => {
    if (error) {
      setTimeout(() => setError(false), 3000);
    }
  }, [error]);
  
  const httpHandler = useCallback(e => {
    setHttp(e.currentTarget.value);
  }, [setHttp]);
  
  
  const nameHandler = useCallback(e => {
    setName(e.currentTarget.value);
  }, [setName]);
  
  
  const descHandler = useCallback(e => {
    setDesc(e.currentTarget.value);
  }, [setDesc]);
  
  const keyHandler = useCallback(e => {
    setKeyWords(e.currentTarget.value);
  }, [setKeyWords]);
  
  
  const submit = e => {
    if ((e.key === 'Enter' || e.type === 'click') && http.trim() && name.trim()) {
      // eslint-disable-next-line no-useless-escape
      const regExp = /^((ftp|http|https):\/\/)?(www\.)?([A-Za-zÐ-Ð¯Ð°-Ñ0-9]{1}[A-Za-zÐ-Ð¯Ð°-Ñ0-9\-]*\.?)*\.{1}[A-Za-zÐ-Ð¯Ð°-Ñ0-9-]{2,8}(\/([\w#!:.?+=&%@!\-\/])*)?/;
      const isValid = regExp.test(http);
      if (isValid) {
        addPhoto(http, name, desc, keyWords);
        setHttp('');
        setName('');
        setDesc('');
        setKeyWords('');
      } else {
        setHttp('');
        setName('');
        setDesc('');
        setKeyWords('');
        setError(true);
      }
    }
  };
  
  return (
    <div className={classes.frame} ref={frame} onClick={closeFrame}>
      <div className={classes.photoAddFrame}>
        <div className={classes.frameContent}>
          <div className={classes.title}>Ð”Ð¾Ð±Ð°Ð²Ð¸Ñ‚ÑŒ Ð½Ð¾Ð²Ð¾Ðµ Ñ„Ð¾Ñ‚Ð¾</div>
          <div className={classes.frameText}>
            <Input fn={submit} value={http} desc='Ð¡ÑÑ‹Ð»ÐºÐ° Ð½Ð° Ñ„Ð¾Ñ‚Ð¾' handler={httpHandler} classN='margin' />
            {error && <span className={classes.error}>ÐŸÐ¾Ð¿Ñ€Ð¾Ð±ÑƒÐ¹Ñ‚Ðµ Ð´Ñ€ÑƒÐ³ÑƒÑŽ ÑÑÑ‹Ð»ÐºÑƒ</span>}
            <Input fn={submit} value={name} desc='ÐÐ°Ð·Ð²Ð°Ð½Ð¸Ðµ' handler={nameHandler} classN='margin' />
            <Input fn={submit} value={desc} desc='ÐžÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ (Ð½ÐµÐ¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾)' handler={descHandler} classN='margin' />
            <Input fn={submit} value={keyWords} desc='ÐšÐ»ÑŽÑ‡ÐµÐ²Ñ‹Ðµ ÑÐ»Ð¾Ð²Ð° (Ð½ÐµÐ¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾)' handler={keyHandler}
                   classN='margin' />
          </div>
          <Button text='Ð”Ð¾Ð±Ð°Ð²Ð¸Ñ‚ÑŒ' classN='photoFrameBtn' fn={submit} />
          <span className={classes.closeFramePhoto}  onClick={() => setShowHandler(false)}>âœ–</span>
        </div>
      </div>
    </div>
  )
};

export default AddPhotoFrame;